<template>
      <v-content >
        <v-container fill-height="fill-height" class="black" >
        <v-layout align-center="align-center" justify-center="justify-center">
          
          <v-flex class="login-form text-xs-center"> 
          <v-row align="center" justify="center">

          <img src="../assets/logo.png" height="70%" width="70%" class="brand-img d-inline-block align-top" alt="Bole-e-tv">
          </v-row>
            <v-card class="black">
              <v-card-text>
                <div class="white--text">
                  <template>Log in to your account</template>
                </div>
                <v-form>
                  <v-row>
                    <v-col cols="12" sm="12" md="12">
                      <v-text-field label="User Name" v-model="user_data.user_name" placeholder="Username" solo></v-text-field>
                      <v-text-field label="Password" v-model="user_data.password" type="Password" placeholder="Password" solo></v-text-field>
                      <v-btn @click="login()" block class="yellow" >
                          <span v-if="loading" class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                          <span v-if="loading" class="sr-only">Loading...</span>
                          <span v-if="!loading" >Login</span>
                      </v-btn>
                    </v-col>
                  </v-row>

                </v-form>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>

      <v-snackbar v-model="snackbar_s" color="success" >
        Succcess
        <v-btn dark text  @click="snackbar_s = false" > Close </v-btn>
      </v-snackbar>
      <v-snackbar v-model="snackbar_f" color="error">
        Error
        <v-btn dark text  @click="snackbar_f = false" > Close </v-btn>
      </v-snackbar>


    </v-content>
</template>
    

<script>

import router from '../router'
import ApiService from '../ApiService'


export default {
    data () {
    return {
      snackbar_s: false,
      snackbar_f: false,

      text: 'Hello, I\'m a snackbar',
            loading : false,
            user_data : {
              "user_name":"",
              "password": ""              
            },
            value: '100',
            area_value: '800',
            enable: true,
          }
    },
    mounted() {
      ApiService.init()
    },
    methods: {
          login(){
          this.loading = true;
          console.log(this.user_data)
          ApiService.post("auth/login",this.user_data)
            .then(response => {
              console.log(response.data)
              console.log(this.user_data)
              localStorage.setItem('auth_token', response.data.auth_token);
              this.snackbar_s = true
              this.user_data = {"user_name": "", "password": "" }
              this.loading = false
              ApiService.init()
              ApiService.get('user')
                .then( response => {
                  this.loading = false
                  console.log(response)
                  const parsed = JSON.stringify(response.data.details);
                  localStorage.setItem('user_data', parsed);
                  router.push('/user/screener')
                }).catch(
                  error => {
                    this.loading = false
                    console.log(error)
                    this.snackbar_f = true
                  }
                )
            })
          .catch(error => {
              this.loading = false
              this.snackbar_f = true
              console.log(error)
          })
      }
    }
  }
</script>

<style>
.login-form {
  max-width: 500px;
  margin: 100px;
}

.yellow{
  color: #ffc107;
}

</style>
