<template>
  <div>
    <v-toolbar>
        <v-toolbar-title>Play Game</v-toolbar-title></v-toolbar>
    <v-container fluid>
    <v-row align="center" class="mt-12">
      <v-col class="mt-n12 mx-auto" cols="12" sm="6">
        <v-select v-model="market" :items="markets" item-text="title" item-value="market_id"  @change="marketSelected" label="Select Market" solo ></v-select>
      </v-col>

      <v-col class="mt-n12 mx-auto" cols="12" sm="6">
        <v-select v-model="currentPlay.game_id" :items="games" item-text="title" item-value="game_id" label="Select Game" solo ></v-select>
      </v-col>

      <v-col class="mt-n12 mx-auto" cols="6" sm="6">
        <v-combobox v-model="currentPlay.game_numbers" :items="forOpen" :search-input.sync="search" label="Numbers" multiple small-chips clearable solo ></v-combobox>

      </v-col>
      <v-col class="mt-n11 mx-auto" cols="6" sm="6">
        <v-text-field v-model="currentPlay.token" label="Solo" placeholder="Amount" solo ></v-text-field>
      </v-col>
      <v-col class="mt-n12 mx-auto"  cols="12" sm="12">
        <v-btn @click="add" color="info" block class="m-4 white--text">  Add
          <v-icon right dark>mdi-arrow-down-thick</v-icon>
        </v-btn>
      </v-col>

      <v-col class="mx-auto"  cols="12" sm="12">
              <v-card  class="mx-auto" >
                <v-simple-table >
                    <template v-slot:default>
                      <thead>
                        <tr>
                          <th class="text-left">Game</th>
                          <th class="text-left">Numbers</th>
                          <th class="text-left">Tokens</th>          
                          <th class="text-left">Total</th>
                          <th class="text-left">Actions</th>                    
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="game in currentPlayed" :key="game.game_id">
                          <td>{{ game.game_title }}</td>
                          <td>
                            <span class="text-left">
                              <v-chip v-for="number in game.game_numbers" class="ma-2" color="primary" label text-color="white" > 
                                {{number}} </v-chip>
                            </span>
                          </td>
                          <td>{{ game.token }}</td>
                          <td>{{ game.token_total }}</td>          
                          <td>
                            <v-btn small @click="deletePlayedGame(game.game_id)" color="error">Remove</v-btn>                              
                          </td>          

                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>

                  <v-row>
                    <v-col cols="10" md="9" sm="7"></v-col>
                    <v-col cols="2" md="3" sm="5"> Total : <v-chip pill> <v-avatar left color="red">T</v-avatar> {{total}}</v-chip> </v-col>
                  </v-row>

                  <v-card-actions>
                    <v-btn @click="play()" color="success" block class="m-4 white--text">  Bid
                      <v-icon right dark>mdi-cursor-pointer</v-icon>
                    </v-btn>
                  </v-card-actions>
              </v-card>
      </v-col>
    </v-row>
  </v-container>  
  </div>

</template>

<script>
import router from '../router'
import ApiService from '../ApiService'

  export default {
    data: () => ({
      items: ['Foo', 'Bar', 'Fizz', 'Buzz'],
      search:null,
      market:-1,
      game:-1,
      markets:[],
      games:[],
      numbers:[],
      forOpen:[],
      forDouble:[],
      canplay:false,
      played:[],
      currentPlayed:[],
      currentPlay:{
        game_title:null,
        game_id:null,
        game_numbers:[],
        token:null,
        token_total:null
      },
      total:0,

    }),    
    mounted() {
      ApiService.init()
      this.loadMarkets()
    },
        methods: {
      play(){
        //Clear Game
        this.played = []
        //Games array
        this.currentPlayed.forEach(game => {
          game.game_numbers.forEach(number => {
            this.played.push({           	
              "game_id":parseInt(game.game_id, 10) ,
              "game_number": parseInt(number, 10) ,
              "tokens":parseInt(game.token, 10) 
              })
            console.log(game.game_id,number,game.token)            
          });
        });
        console.log(this.played)
        //Post
          ApiService.post("game",this.played)
                .then(response => {
                    console.log(response.data)
                })
            .catch(error => {
                console.log(error)
            })



        
      },
      add(){
        this.currentPlay.token_total = this.currentPlay.token * this.currentPlay.game_numbers.length
        this.currentPlay.game_title = this.getGameTitle(this.currentPlay.game_id)
        this.currentPlayed.push(this.currentPlay)
        this.currentPlay = {
          game_title:null,
          game_id:null,
          game_numbers:[],
          token:null,
          token_total:null
        }
        console.log(this.played)
        this.updateTotal()

      },
        deletePlayedGame(game_id) {
            for (var i = 0; i < this.currentPlayed.length; i++) {
                if (game_id === this.currentPlayed[i].game_id ) {
                    this.currentPlayed.splice(i, 1);
                }
            }
            this.updateTotal()
        },
        updateTotal() {
            this.total = 0
            for (var i = 0; i < this.currentPlayed.length; i++) {
                
                this.total += parseInt(this.currentPlayed[i].token_total, 10)
            }
        },
      loadMarkets(){
          ApiService.get("market")
            .then( response => {
              this.markets = response.data.details
              console.log(response.data)              
            }).catch(
              error => {
                console.log(error)
              }
            )
      },
      loadGame(){

      },
      marketSelected(){
        ApiService.get("market/"+this.market)
          .then( response => {
            this.games = response.data.details
            console.log(response.data)              
          }).catch(
            error => {
              console.log(error)
            }
          )
        console.log("Market Selected",this.market)
      },
      numberEntered(){
        console.log(this.numbers)
      },
    input(value){
      console.log("Input",value)
      //this.inputed.push(value)
    },
    getGameTitle(id){
      for(var i=0;i<this.games.length;i++){
        if(this.games[i].game_id==id){
          return this.games[i].title
        }
      }
      return null
    }
    },
    watch:{
      numbers(){
        console.log('Watch',this.numbers)
      },
      search(){
        if(this.search.length == 2){
          this.currentPlay.game_numbers.push(this.search)
          this.search = ""
        }
      }
    }
  }
</script>